'use client';

import React, { useState, useCallback, useEffect } from 'react';
import { SidebarProvider, SidebarInset, SidebarTrigger } from '@/components/ui/sidebar';
import { AppSidebar } from '@/components/AppSidebar';
import CustomCursor from '@/components/CustomCursor';
import AnimatedBackground from '@/components/AnimatedBackground';
import AccessRequestModal from '@/components/AccessRequestModal';
import Toast from '@/components/Toast';
import { useToast } from '@/hooks/useToast';
import './globals.css';

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalSourcePage, setModalSourcePage] = useState('');
  const { toast, showToast, hideToast } = useToast();

  const openModal = useCallback((sourcePage: string) => {
    setModalSourcePage(sourcePage);
    setIsModalOpen(true);
  }, []);

  const closeModal = () => {
    setIsModalOpen(false);
  };

  // Listen for custom events from pages
  useEffect(() => {
    const handleOpenModal = (event: Event) => {
      const customEvent = event as CustomEvent;
      const source = customEvent.detail?.source || 'unknown';
      openModal(source);
    };

    window.addEventListener('openAccessModal', handleOpenModal);
    return () => {
      window.removeEventListener('openAccessModal', handleOpenModal);
    };
  }, [openModal]);

  return (
    <html lang="en">
      <head>
        <meta charSet="UTF-8" />
        <meta name="viewport" content="width=device-width, initial-scale=1.0" />
        <title>Nucigen Labs  Strategic Intelligence</title>
        <meta name="title" content="Nucigen Labs  Strategic Intelligence" />
        <meta name="description" content="We scan the news. We predict the market. Before it moves. Transform global news into predictive market signals in real-time." />
        <meta name="keywords" content="market intelligence, predictive analytics, geopolitical analysis, financial forecasting, strategic intelligence" />
        <meta name="author" content="Nucigen Labs" />
        <meta name="robots" content="index, follow" />
        <link rel="canonical" href="https://nucigenlabs.com" />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://nucigenlabs.com" />
        <meta property="og:title" content="Nucigen Labs  Strategic Intelligence" />
        <meta property="og:description" content="We scan the news. We predict the market. Before it moves. Transform global news into predictive market signals in real-time." />
        <meta property="og:image" content="https://nucigenlabs.com/og-image.png" />
        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:url" content="https://nucigenlabs.com" />
        <meta name="twitter:title" content="Nucigen Labs  Strategic Intelligence" />
        <meta name="twitter:description" content="We scan the news. We predict the market. Before it moves." />
        <meta name="twitter:image" content="https://nucigenlabs.com/og-image.png" />
        <meta name="theme-color" content="#0A0A0A" />
      </head>
      <body>
        <SidebarProvider>
          <div className="relative min-h-screen">
            <AnimatedBackground />
            <CustomCursor />
            <AppSidebar onRequestAccess={() => openModal('sidebar')} />
            
            <SidebarInset>
              <header className="flex h-16 shrink-0 items-center gap-2 border-b border-sidebar-border px-4">
                <SidebarTrigger className="-ml-1" />
              </header>
              
              <div className="flex flex-1 flex-col">
                {toast.isVisible && (
                  <Toast
                    message={toast.message}
                    type={toast.type}
                    onClose={hideToast}
                  />
                )}

                <AccessRequestModal
                  isOpen={isModalOpen}
                  onClose={closeModal}
                  onSuccess={(message) => showToast(message, 'success')}
                  onError={(message) => showToast(message, 'error')}
                  sourcePage={modalSourcePage}
                />

                {children}
              </div>
            </SidebarInset>
          </div>
        </SidebarProvider>
      </body>
    </html>
  );
}

