import Link from "next/link"
import { usePathname } from "next/navigation"
import {
  Terminal,
  TrendingUp,
  FileText,
  DollarSign,
  Layers,
} from "lucide-react"
import {
  Sidebar,
  SidebarContent,
  SidebarFooter,
  SidebarGroup,
  SidebarGroupContent,
  SidebarGroupLabel,
  SidebarHeader,
  SidebarMenu,
  SidebarMenuButton,
  SidebarMenuItem,
} from "./ui/sidebar"
import { Button } from "./ui/Button"

const navItems = [
  { path: "/", label: "Platform", icon: Terminal },
  { path: "/intelligence", label: "Intelligence", icon: Layers },
  { path: "/case-studies", label: "Case Studies", icon: TrendingUp },
  { path: "/papers", label: "Research", icon: FileText },
  { path: "/pricing", label: "Pricing", icon: DollarSign },
]

interface AppSidebarProps {
  onRequestAccess?: () => void
}

export function AppSidebar({ onRequestAccess }: AppSidebarProps) {
  const pathname = usePathname()

  return (
    <Sidebar collapsible="icon">
      <SidebarHeader>
        <Link
          href="/"
          className="group flex items-center gap-2 text-white font-light text-xl tracking-tight"
        >
          <div className="relative">
            <div className="absolute inset-0 bg-gradient-to-br from-red-500/20 to-orange-500/20 blur-lg group-hover:blur-xl transition-all duration-300"></div>
            <Terminal
              size={24}
              className="relative text-red-400 group-hover:text-red-300 transition-colors"
            />
          </div>
          <span className="relative group-data-[collapsible=icon]:hidden">
            NUCIGEN
            <div className="absolute -bottom-1 left-0 w-0 h-[1px] bg-gradient-to-r from-red-400 to-orange-400 group-hover:w-full transition-all duration-300"></div>
          </span>
        </Link>
      </SidebarHeader>
      <SidebarContent>
        <SidebarGroup>
          <SidebarGroupLabel>Navigation</SidebarGroupLabel>
          <SidebarGroupContent>
            <SidebarMenu>
              {navItems.map((item) => {
                const Icon = item.icon
                const isActive = pathname === item.path

                return (
                  <SidebarMenuItem key={item.path}>
                    <SidebarMenuButton
                      asChild
                      isActive={isActive}
                      tooltip={item.label}
                    >
                      <Link href={item.path}>
                        <Icon />
                        <span>{item.label}</span>
                      </Link>
                    </SidebarMenuButton>
                  </SidebarMenuItem>
                )
              })}
            </SidebarMenu>
          </SidebarGroupContent>
        </SidebarGroup>
      </SidebarContent>
      <SidebarFooter>
        <Button
          className="w-full bg-gradient-to-r from-red-600/90 to-red-500/90 hover:from-red-500 hover:to-red-400 text-white"
          variant="default"
          onClick={onRequestAccess}
        >
          Request Access
        </Button>
      </SidebarFooter>
    </Sidebar>
  )
}

